import {
	mapState,
	mapMutations,
	mapActions
} from 'vuex'
import tuiIcon from "@/components/icon/icon"
import tuiButton from "@/components/button/button"
const util = require('../../utils/util.js')
export default {
	components: {
		tuiIcon,
		tuiButton
	},
	data() {
		return {
			disabled: false,
			btnText: "获取验证码",
			mobile: "",
			type: "primary",
			code: ""
		}
	},
	methods: {
		...mapMutations(['login']),
		getRandom: function(u) {
			let rnd = "";
			u = u || 6;
			for (var i = 0; i < u; i++) {
				rnd += Math.floor(Math.random() * 10);
			}
			return Number(rnd);
		},
		doLoop: function(seconds) {
			let code = this.getRandom(6);
			this.tui.toast('您本次的验证码是：' + code, 5000);
			seconds = seconds ? seconds : 60;
			this.btnText = seconds + "s后获取";
			this.code = code
			let countdown = setInterval(() => {
				if (seconds > 0) {
					this.btnText = seconds + "s后获取"
						--seconds;
				} else {
					this.btnText = "获取验证码";
					this.disabled = false;
					this.type = "primary";
					clearInterval(countdown);
				}
			}, 1000);
		},
		btnSend: function() {
			if (util.isNullOrEmpty(this.mobile)) {
				this.tui.toast('请输入手机号码');
				return
			} else if (!util.isMobile(this.mobile)) {
				this.tui.toast('请输入正确的手机号码');
				return
			}
			this.disabled = true;
			this.btnText = "请稍候...";
			this.type = "gray"

			setTimeout(() => {
				this.doLoop(60)
			}, 500)
		},
		formLogin: function(e) {
			let loginCode = e.detail.value.smsCode;
			let mobile = e.detail.value.mobile;
			if (util.isNullOrEmpty(mobile)) {
				this.tui.toast('请输入手机号码');
				return
			} else if (!util.isMobile(mobile)) {
				util.toast('请输入正确的手机号码');
				return
			} else if (util.isNullOrEmpty(loginCode)) {
				this.tui.toast('请输入验证码');
				return
			} else if (loginCode != this.code) {
				this.tui.toast('验证码不正确');
				return
			}
			uni.setStorageSync("thorui_mobile", util.formatNum(mobile));
			let state = {
				mobile: util.formatNum(mobile),
				isLogin: true,
				memberId: 0
			};
			this.login(state);
			this.tui.toast("登录成功", 2000, true);
			setTimeout(() => {
				uni.reLaunch({
					url: '../my/my'
				})
			}, 200);
		},
		protocol: function() {
			uni.navigateTo({
				url: '../about/about'
			})
		}
	}
}
